.burger {
  position: relative;
  width: 3rem;
  height: 3rem;
  background-color: unset;
  z-index: 20;
}

.burger_open,
.burger_close {
    position: absolute;
    left: 0;
}

.burger_close:after {
    opacity: 0;
}

.burger_open:after,
.burger_close:after,
.burger_close:before  {
    content: "";
    position: absolute;
    background: var(--color-graphite);
    width: 3rem;
    height: 0.2rem;
    transition: opacity var(--default-transition);
}

.burger_open:after {
    box-shadow: 0 -1rem var(--color-graphite), 0 1rem var(--color-graphite);
}

.active .burger_open:after {
  opacity: 0;
  transition: opacity var(--default-transition);
} 

.active .burger_close:after {
  opacity: 1;
  transition: opacity var(--default-transition);
} 

.active .burger_close:after {
  transform: rotate(-45deg);
}

.active .burger_close:before {
  transform: rotate(45deg);
}
