.products-preview {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 1rem;

	@include vp-1280 {
		grid-template-columns: 1fr;
	}

	@include vp-1140 {
		grid-template-columns: 1fr 1fr;
	}

	@include vp-768 {
		grid-template-columns: 1fr;
	}
}

.products-preview__item {
	background-color: var(--color-white);
	border-radius: 3rem;
}

.products-preview__link {
	display: flex;
	align-items: flex-start;
	gap: 1.3rem;
	padding: 1.6rem;

	@include hover;
}

.products-preview__img {
	max-width: 7.5rem;
	object-fit: contain;

	@include vp-1140 {
		max-width: 5.5rem;
	}
}

.products-preview__description {
	flex-grow: 1;
}

.products-preview__wrap {
	display: flex;
	justify-content: space-between;
	margin-top: 0.8rem;
}

.products-preview__price {
	position: relative;
	font-size: 2.5rem;
	font-weight: 700;

	@include vp-1140 {
		font-size: 2rem;
	}

	&::after {
		content: '';
		position: absolute;
		width: 1.6rem;
		height: 2.5rem;
		right: -2rem;
		top: 0.1rem;
		background-image: url('../assets/svg/rub.svg');
		background-size: contain;
		background-repeat: no-repeat;

		@include vp-1140 {
			right: -1.7rem;
			width: 1.3rem;
			top: 0;
		}
	}
}
