.header-menu {
    display: flex;
    align-items: center;
    padding: 1.6rem 1.7rem 1.3rem 1.7rem;
    background-color: var(--color-gray);
    border-radius: 10rem;
    font-size: 1.8rem;

    @include vp-1440 {
        flex-direction: column;
        align-items: flex-start;
        position: absolute;
        z-index: 10;
        left: calc(-120%);
        top: 0;
        width: calc(100vw);
        height: 100dvh;
        border-radius: 0;
        opacity: 0;
        transition: var(--default-transition);

        &.active {
            left: 0;
            opacity: 1;
        }
	}
}

.header-menu__link {
    padding: 1.5rem 2.15rem;
    @include hover;

    &.active {
        color: var(--color-white);
        background-color: var(--color-green);
        border-radius: 10rem;
    }
}