.popular {
	margin-top: -1.5rem;

	@include vp-1600 {
		margin-top: 2rem;
	}
}

.popular__title {
	margin-bottom: 6rem;
	font-size: 5.1rem;
	font-weight: 400;

	@include vp-1140 {
		font-size: 3rem;
	}
}

.popular__wrap {
	display: grid;
	grid-template-columns: 23.6% auto 23.6%;
	grid-template-rows: repeat(4, 1fr);
	gap: 1rem;

	@include vp-1440 {
		grid-template-columns: 27% auto 25%;
	}

	@include vp-1140 {
		grid-template-columns: 1fr 1fr;
		grid-template-rows: unset;

		.card {
			width: 100%;
		}
	}

    @include vp-575 {
		display: flex;
        flex-direction: column  ;
	}
}

.popular__card-1,
.popular__card-3,
.popular__card-6 {
	grid-row: span 2;

	@include vp-1140 {
		grid-row: auto;
	}
}

.popular__card-2 {
	grid-row: span 1;

	@include vp-1140 {
		grid-column: span 2;
	}
}

.popular__card-3 {
	@include vp-1140 {
		grid-column: 2 /3;
        grid-row: 1 / 2;
	}
}

.popular__card-4 {
	grid-row: 3 / 5;
	@include vp-1140 {
		grid-row: auto;
	}
}

.popular__card-5 {
	grid-row: span 2 / 4;
	@include vp-1140 {
		grid-row: auto;
		grid-column: span 2;
	}
}

.popular__card-6 {
	@include vp-1140 {
		grid-column: 2 /3;
        grid-row: 3 / 4;
	}
}

.popular__card-7 {
	@include vp-1140 {
		grid-column: span 2;
	}
}

.popular__card {
	position: relative;
	min-height: 49.3rem;
	background-size: cover;
	background-image: url('../assets/svg/bg2.svg');
	@include vp-1140 {
		background-size: unset;

		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 50%;
			height: 100%;
			background-color: var(--color-gray);
			border-radius: 3rem;
		}
	}
}

.popular__card-head {
	justify-content: start;
	gap: 1.6rem;
	align-items: center;
}

.popular__banner {
	display: flex;
	padding: 1rem;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	border-radius: 3rem;
	overflow: hidden;
	min-height: 50.8rem;

	@include vp-1140 {
		min-height: 24rem;
	}
}

.popular__banner-link {
	margin: auto;
	font-size: 5.1rem;
	padding: 3rem;
	color: var(--color-white);
	background: rgba(255, 255, 255, 0.25);
	backdrop-filter: blur(19.1538px);
	border-radius: 3rem;
	text-align: center;

	@include hover;

	@include vp-1440 {
		font-size: 3rem;
	}

	@include vp-1140 {
		border-radius: 2rem;
	}
}
