.info {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 1rem;

	@include vp-1140 {
		grid-template-columns: 1fr;
	}
}

.info__description {
	display: flex;
	flex-direction: column;
	padding: 3rem;
	background-color: var(--color-white);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	border-radius: 3rem;
	min-height: 46.3rem;

	@include vp-1140 {
		padding: 2rem;
		min-height: 30rem;
	}
}

.info__description-title {
	font-size: 5.1rem;
	font-weight: 400;

	@include vp-1140 {
		font-size: 3rem;
	}
}

.info__description-button {
	display: inline-flex;
	gap: 1rem;
	padding: 1.4rem;
	border-radius: 3rem;
	font-size: 2.1rem;
	color: var(--color-white);
	background-color: var(--color-green);

	@include hover;

	@include vp-1140 {
		padding: 1rem;
		font-size: 1.6rem;

		img {
			transform: scale(0.7);
		}
	}
}

.info__description-text {
	margin-top: 1.6rem;
	margin-bottom: 1.6rem;
	max-width: 60rem;
	font-size: 3.1rem;

	@include vp-1140 {
		font-size: 2.2rem;
	}
}

.info__description-wrap {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-top: auto;

	@include vp-1600 {
		gap: 1rem;
	}
}

.info__description-about-wrap {
	display: flex;

	@include vp-1600 {
		gap: 1rem;
	}
}

.info__description-about {
	display: flex;
	flex-direction: column;
	gap: 0.5rem;

	&:first-child {
		margin-right: 6rem;

		@include vp-1600 {
			margin-right: 0;
		}
	}

	span {
		font-size: 3.9rem;
		font-weight: 700;

		@include vp-1140 {
			font-size: 2.8rem;
		}
	}

	p {
		font-size: 2.5rem;

		@include vp-1140 {
			font-size: 1.8rem;
		}
	}
}

.info__description-top-wrap {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	margin-bottom: auto;
}

.info__products-preview {
	margin-top: 2rem;
}

.info__bottom {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 1rem;
	margin-top: 1rem;

	@include vp-1140 {
		gap: 0;
	}
}

.info__slider {
	@include vp-1140 {
		overflow: visible;
	}
}
