.description-block__top {
	padding: 4rem;
	border-radius: 4rem 4rem 0 4rem;
	background-color: var(--color-gray);

	@include vp-1140 {
		padding: 2rem;
		border-radius: 2rem 2rem 0 0;
	}
}

.description-block__bottom {
	position: relative;
	padding: 2rem 4rem 4rem 4rem;
	margin-left: auto;
	background-color: var(--color-gray);
	border-radius: 0 0 4rem 4rem;
	max-width: 62rem;
	font-size: 2.6rem;

	@include vp-1140 {
		border-radius: 0 0 2rem 2rem;
		padding: 0 2rem 2rem 2rem;
		max-width: unset;
		font-size: 1.8rem;
	}

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: -2.9rem;
		border-radius: 0 20px 1px 0;
		box-shadow: 15px -1rem 0 1rem var(--color-gray);
		width: 3rem;
		height: 3rem;

		@include vp-1024 {
			content: none;
		}
	}
}
