.card {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	position: relative;
	padding: 3rem;
	background-repeat: no-repeat;
	background-position: bottom right;
	background-image: url('../assets/img/image8.png');
	border-radius: 3rem;
	min-height: 46.3rem;
	overflow: hidden;

	@include vp-1140 {
		padding: 2rem;
		width: 39vw;
		min-height: 34rem;
	}

	@include vp-600 {
		padding: 2rem;
		width: 74vw;
	}

	&--white-bg {
		background-color: var(--color-white);
		background-image: none;
	}

	&--full-bg {
		background-size: cover;
	}

	&--horizontal {
		max-height: 23.4rem;
		min-height: unset;
		background-size: cover;
		background-image: url('../assets/svg/bg1.svg');

		@include vp-1140 {
			background-size: unset;
			min-height: 24rem;
			&::before {
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 50%;
				background-color: var(--color-gray);
				border-radius: 3rem;
			}

			&::after {
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				width: 50%;
				height: 100%;
				background-color: var(--color-gray);
				border-radius: 3rem;
			}
		}
	}

	&:has(.card__button) {
		overflow: visible;
	}
}

.card__head {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	flex-wrap: wrap;
	gap: 1rem;
	z-index: 1;
}

.card__title {
	font-size: 3.1rem;
	color: var(--color-white);
	font-weight: 400;
	z-index: 1;

	@include vp-1140 {
		font-size: 2rem;
	}

	&--black {
		color: var(--color-graphite);
	}
}

.card__img {
	position: absolute;
	bottom: 0;
	margin-left: auto;
	margin-right: auto;
	left: 0;
	right: 0;

	&--right {
		left: unset;
	}
}

.card__text {
	margin-left: 7rem;
	margin-right: 4rem;
	margin-top: auto;
	z-index: 1;

	@include vp-1440 {
		margin-left: 0;
	}
}

.card__button {
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	right: 0;
	bottom: 0;
	width: 5.4rem;
	height: 5.4rem;
	background-color: var(--color-green);
	border-radius: 2rem;

	@include hover;
}

.card__footer {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	justify-content: space-between;
	gap: 1rem;
	z-index: 1;
}

.card__social-wrap {
	display: flex;
	gap: 1rem;
	z-index: 1;
}

.card__link {
	display: flex;
	gap: 1rem;
	align-items: center;
	z-index: 1;
	@include hover;
}
