.circle-list {
	display: flex;
	flex-direction: row-reverse;
}

.circle-list__item {
	display: flex;

	align-items: center;
	justify-content: center;
	width: 6.6rem;
	height: 6.6rem;
	background-color: var(--color-gray);
	border-radius: 50%;
	border: 0.1rem solid var(--color-white);

	@include vp-1140 {
		width: 4.8rem;
		height: 4.8rem;
	}

	&:not(:first-child) {
		margin-right: -1.2rem;
	}
}
