.header {
	padding: 2rem 0;
    position: relative;
}

.header__wrap {
	display: flex;
	align-items: center;
}

.header__logo-wrap {
	display: flex;
	gap: 1.2rem;
	align-items: center;

	@include hover;
}

.header__logo-img {
    min-width: 4.6rem;
}

.header__logo-title {
	@include vp-768 {
		display: none;
	}
}

.header__menu {
	margin-left: 13vw;

	@include vp-1600 {
		margin-left: 3vw;
	}

    @include vp-1440 {
        margin-left: 0;
    }
}

.header__icons-wrap {
	display: flex;
	gap: 1rem;
	margin-left: auto;
}

.header__burger {
    display: none;
    @include vp-1440 {
		display: block;
        margin-left: 3rem;
	}
}